
import { defineComponent } from 'vue';
import {
  Button, Message,
} from '@/components';
import Container from './component/container.vue';

export default defineComponent({
  name: 'MessageExample',
  components: {
    KiButton: Button,
    Container,
  },
  setup() {
    let hide: any;
    return {
      click1() {
        Message.info('消息提示');
      },
      click2(info: string) {
        Message.success(info);
      },
      click3(info: string) {
        Message.error(info);
      },
      click4(info: string) {
        Message.info(info);
      },
      click5(info: string) {
        Message.warning(info);
      },
      click6() {
        hide = Message.warning({
          content: '这是一个不会关闭的提示',
          delay: -1,
        });
      },
      close() {
        hide();
      },
      click7() {
        Message.warning({
          content: '10s 后关闭',
          delay: 10000,
        });
      },
      click8(info: string) {
        Message.success({
          content: info,
          showIcon: true,
        });
      },
      click9(info: string) {
        Message.error({
          content: info,
          showIcon: true,
        });
      },
      click10(info: string) {
        Message.info({
          content: info,
          showIcon: true,
        });
      },
      click11(info: string) {
        Message.warning({
          content: info,
          showIcon: true,
        });
      },
    };
  },
});
