
import { defineComponent, ref, onMounted } from 'vue';
import ButtonExample from './button.vue';
import IconExample from './icon.vue';
import RadioExample from './radio.vue';
import TagExample from './tag.vue';
import SelectExample from './select.vue';
import InputExample from './input.vue';
import FormExample from './form.vue';
import UploadExample from './upload.vue';
import MessageExample from './message.vue';
import PaginationExample from './page.vue';
import ModalExample from './modal.vue';
import AlertExample from './alert.vue';
import CarouselExample from './carousel.vue';
import CalendarExample from './calendar.vue';

export default defineComponent({
  name: 'Examples',
  components: {
    ButtonExample,
    IconExample,
    RadioExample,
    TagExample,
    SelectExample,
    InputExample,
    FormExample,
    UploadExample,
    MessageExample,
    PaginationExample,
    ModalExample,
    AlertExample,
    CarouselExample,
    CalendarExample,
  },
  setup() {
    const componentName = ref('');
    const contentRef = ref<null | HTMLDivElement>(null);
    const components = [
      {
        name: 'IconExample',
        title: 'Icon 图标',
      },
      {
        name: 'ButtonExample',
        title: 'Button 按钮',
      },
      {
        name: 'TagExample',
        title: 'Tag 标签',
      },
      {
        name: 'RadioExample',
        title: 'Radio 单选框',
      },
      {
        name: 'SelectExample',
        title: 'Select 选择器',
      },
      {
        name: 'InputExample',
        title: 'Input 输入框',
      },
      {
        name: 'UploadExample',
        title: 'Upload 文件上传',
      },
      {
        name: 'FormExample',
        title: 'Form 表单',
      },
      {
        name: 'MessageExample',
        title: 'Message 消息提示',
      },
      {
        name: 'PaginationExample',
        title: 'Pagination 分页器',
      },
      {
        name: 'ModalExample',
        title: 'Modal 弹出框',
      },
      {
        name: 'AlertExample',
        title: 'Alert 警告',
      },
      {
        name: 'CarouselExample',
        title: 'Carousel 走马灯',
      },
      {
        name: 'CalendarExample',
        title: 'Calendar 日历',
      },
    ];
    const handleClick = (name: string) => {
      componentName.value = name;
      window.location.hash = `/${name}`;
      if (contentRef.value) {
        contentRef.value.scrollTo({
          top: 0,
          behavior: 'smooth',
        });
      }
    };
    onMounted(() => {
      // eslint-disable-next-line prefer-destructuring
      componentName.value = window.location.hash.split('/')[1];
    });
    return {
      contentRef,
      components,
      componentName,
      handleClick,
    };
  },
});
